.heroSinglePost__section {
  position: relative;
  padding: 152px 0 160px 0;
  background-color: #f4f5f6;
  margin-top: -92px;
}

.heroSinglePost__wraper--content {
  position: relative;
  z-index: 1;
}

.heroSinglePost__title {
  margin-top: 40px;
  color: #1e212c;

  font-family: Ubuntu;
  font-size: 64px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: 1px;

  @media (max-width: 600px) {
    font-size: 44px;
    text-align: center;
  }
}

.heroSinglePost__wrapper--infoBlock {
  margin-top: 48px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: 600px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.heroSinglePost__sicialBlock--list {
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 600px) {
    margin-top: 24px;
  }
}

.heroSinglePost__sicialBlock--item:not(:last-child) {
  margin-right: 12px;
}

.heroSinglePost__wrapper--date {
  display: flex;
  align-items: center;

  color: #787a80;
  font-family: Ubuntu;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;

  @media (max-width: 450px) {
    flex-direction: column;
  }
}

.heroSinglePost__category {
  position: relative;
  margin-right: 25px;
  color: #ff5a30;

  transition: color 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}

.heroSinglePost__category::after {
  content: "";
  position: absolute;
  bottom: 2px;
  right: -13px;
  background: #787a80;
  width: 1px;
  height: 12px;
}

.heroSinglePost__date {
  position: relative;
  margin-right: 25px;
}

.heroSinglePost__date::after {
  content: "";
  position: absolute;
  bottom: 2px;
  right: -13px;
  background: #787a80;
  width: 1px;
  height: 12px;
}

.heroSinglePost__comments {
  margin-left: 8px;
}

.heroSinglePost__social--icon {
  display: inline-block;
  transition: transform 0.3s ease;
}

.heroSinglePost__social--icon:hover,
.heroSinglePost__social--icon:focus {
  transform: scale(1.3);
}

.heroSinglePost__wrapper--categoryAndDate {
  display: flex;
  @media (max-width: 450px) {
    justify-content: center;
    align-items: center;
  }
}

.heroSinglePost__wrapper--comments {
  display: flex;
  justify-content: center;
  align-items: center;
}
